import { connect } from 'react-redux';
import App from './App';

import {socketConnected, socketDisconnected} from '../actions/socketAction'

import { playersListUpdated } from '../actions/playerConnectionAction';
import { timerInfoUpdated } from '../actions/timerInfoAction';
import { isLoggedInUpdated, loginInfoUpdated } from '../actions/loginInfoAction';

const mapStateToProps = state => {
    return {
        conn: state.connection,

        isLoggedIn: state.login.is_logged_in
    }
}

const mapDispatchToProps = (dispatch) => {
    // Actions to change the state
    return {
        // Socket connection
        socketConnected: (clientId, s) => dispatch(socketConnected(clientId, s)),
        socketDisconnected: () => dispatch(socketDisconnected()),

        updatePlayersList: (playersList) => dispatch(playersListUpdated(playersList)),
        updateTimerInfo: (timerInfo) => dispatch(timerInfoUpdated(timerInfo)),


        updateIsLoggedIn: (isLoggedIn) => dispatch(isLoggedInUpdated(isLoggedIn)),
        updateUsernameAndPassword: (username, password) => dispatch(loginInfoUpdated(username, password)),
        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);