import { connect } from 'react-redux';
import AuctionArea from "./AuctionArea";
import { sendRelaunchAuction, sendStartAuction } from '../../actions/socket/socketSendAuctionAction';

const mapStateToProps = state => {
    return {
        conn: state.connection,
        timer_info: state.game.timer,
        myUsername: state.login.username
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        // timerTerminated: (isTerminated) => dispatch(activeTimerCycleTerminated(isTerminated)),

        sendStart: (clientId, socket, myUsername) => dispatch(sendStartAuction(clientId, socket, myUsername)),
        sendRelaunch: (clientId, socket, myUsername) => dispatch(sendRelaunchAuction(clientId, socket, myUsername)),

    }

}

export default connect(mapStateToProps, mapDispatchToProps)(AuctionArea);