import React from 'react'

import styles from './AuctionArea.module.css';

class AuctionArea extends React.Component {

  render() {
    // console.log("AuctionArea - Rendering with props:", this.props)

    let timerInfo = this.props.timer_info
    let myUsername = this.props.myUsername

    let isStartDisabled = timerInfo.is_started
    // let isRelaunchDisabled = !timerInfo.is_started || myUsername == timerInfo.holder
    let isRelaunchDisabled = !timerInfo.is_started

    return (
      <div className={styles.AuctionArea}>
        <div className={styles.AuctionBox}>
          <p className={styles.TimerText}>
            {timerInfo.current_value || "0"}
          </p>
        </div>

        <div className={` ${styles.AuctionBox} ${styles.RelaunchBox} `}>
          <button className={` ${styles.RelaunchButton} ${styles.RelaunchBox} ${isRelaunchDisabled ? styles.AuctionBoxDisabled : ""} `}
                  onClick={() => this.props.sendRelaunch(this.props.conn.client_id,
                                                         this.props.conn.socket,
                                                         myUsername)}
            disabled={isRelaunchDisabled}
          >
            RILANCIA
          </button>
        </div>

        <div className={styles.AuctionBox}>
          <p className={styles.HolderText}>
            {timerInfo.holder || "-"}
          </p>
        </div>

        <div className={` ${styles.AuctionBox} ${styles.StartAuctionBox} `}>
          <button className={` ${styles.StartButton} ${isStartDisabled ? styles.AuctionBoxDisabled : ""} `}
                  onClick={() => this.props.sendStart(this.props.conn.client_id,
                                                      this.props.conn.socket,
                                                      myUsername)}
            disabled={isStartDisabled}                                            
          >
            AVVIA ASTA
          </button>
        </div>
        
      </div>
    )
  }
}

export default AuctionArea