import { connect } from 'react-redux';
import LoginModal from './LoginModal';
import { loginUsernameUpdated, loginPasswordUpdated } from '../../actions/loginInfoAction';
import { sendLogin } from '../../actions/socket/socketSendLoginAction';

const mapStateToProps = state => {
    return {
        // conn: state.connection,  // This comes from the App.js

        username: state.login.username,
        password: state.login.password,
        
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateUsernameInfo : (evt) => dispatch(loginUsernameUpdated(evt.target.value)),
        updatePasswordInfo : (evt) => dispatch(loginPasswordUpdated(evt.target.value)),

        sendLogin: (clientId, s, username, password) => dispatch(sendLogin(clientId, s, username, password))
    }

}

export default connect(mapStateToProps, mapDispatchToProps)(LoginModal);