import { 
  LOGIN_USERNAME_UPDATED,
  LOGIN_PASSWORD_UPDATED,
  LOGIN_INFO_UPDATED,
  LOGIN_CONNECTION_UPDATED
} from "./action-types";

export const loginUsernameUpdated = (username) => ({
  type: LOGIN_USERNAME_UPDATED,
  value: username
});

export const loginPasswordUpdated = (password) => ({
  type: LOGIN_PASSWORD_UPDATED,
  value: password
});

export const loginInfoUpdated = (username, password) => ({
  type: LOGIN_INFO_UPDATED,
  value: {
    username: username,
    password: password
  }
});


export const isLoggedInUpdated = (isLoggedIn) => ({
  type: LOGIN_CONNECTION_UPDATED,
  value: isLoggedIn
});