import ActionPlayer from "../../socket/model/player/ActionPlayer";
import TypeTimer from "../../socket/model/player/TypeTimer";
import { sendViaSocket } from "./SocketSender";
const { v4: uuid_v4 } = require('uuid');

export const sendStartAuction = (clientId, socket, myUsername) => {
	return () => {
    let msg = {
      request_id: uuid_v4(),
      client_id: clientId,
      socket_id: socket.id,
      action: ActionPlayer.TIMER,
      type: TypeTimer.STARTED,
      payload: {
        my_username: myUsername
      }
    }

    sendViaSocket(socket, msg)
	}	
}

export const sendRelaunchAuction = (clientId, socket, myUsername) => {
	return () => {
    let msg = {
      request_id: uuid_v4(),
      client_id: clientId,
      socket_id: socket.id,
      action: ActionPlayer.TIMER,
      type: TypeTimer.RELAUNCHED,
      payload: {
        my_username: myUsername
      }
    }

    sendViaSocket(socket, msg)
	}	
}