import ActionPlayer from "../../socket/model/player/ActionPlayer";
import TypeLogin from "../../socket/model/player/TypeLogin";
import { sendViaSocket } from "./SocketSender";
const { v4: uuid_v4 } = require('uuid');

export const sendLogin = (clientId, socket, username, password) => {
	return () => {
    let msg = {
      request_id: uuid_v4(),
      client_id: clientId,
      socket_id: socket.id,
      action: ActionPlayer.LOGIN,
      type: TypeLogin.LOGIN,
      payload: {
        username: username,
        password: password
      }
    }

    sendViaSocket(socket, msg)
	}	
}