import { SOCKET_CONNECTED, SOCKET_DISCONNECTED } from "../actions/action-types";

const initialState = {
  client_id: undefined,
  socket: {}
}

export default (state = initialState, action) => {
  let newState

  switch (action.type) {
    case SOCKET_CONNECTED:
      newState = Object.assign({}, state)

      newState.client_id = action.value.client_id
      newState.socket = action.value.socket

      return newState

    case SOCKET_DISCONNECTED:
      newState = Object.assign({}, state)

      newState.socket = {}

      return newState

    default:
      return state
  }
}