import { 
  LOGIN_USERNAME_UPDATED,
  LOGIN_PASSWORD_UPDATED,
  LOGIN_INFO_UPDATED,
  LOGIN_CONNECTION_UPDATED
} 
from "../actions/action-types";

const initialState = {
  username: "",
  password: "",
  is_logged_in: false
}

export default (state = initialState, action) => {
  let newState = Object.assign({}, state)

  switch (action.type) {
    case LOGIN_USERNAME_UPDATED:
      newState.username = action.value
      
      return newState

    case LOGIN_PASSWORD_UPDATED:
      newState.password = action.value
      
      return newState

    case LOGIN_INFO_UPDATED:
      if(newState.username != "" && newState.password != "") {
        console.log("Already assigned, nothing to do!")
        return newState
      }

      newState.username = action.value.username
      newState.password = action.value.password
      
      return newState
  
    case LOGIN_CONNECTION_UPDATED:
      newState.is_logged_in = action.value
      
      return newState

    default:
      return state
  }
}