import React from 'react'
import ReactModal from 'react-modal'

import styles from './LoginModal.module.css';
import ModalOverlayStyle from './ModalOverlayStyles';

class LoginModal extends React.Component {

  render(){
    // console.log("LoginModal - Received props:", this.props)
    
    return <ReactModal className={styles.LoginModal}
                isOpen={true}
                contentLabel="Login Modal"
                ariaHideApp={false}
                style={ModalOverlayStyle}
            >
              
              <div className={styles.TitleDiv}>
                <span className={styles.TitleText}>Benvenuto CAMPIONE</span>
              </div>

              <div className={styles.InputDiv}>
                <div className={styles.InputRow}>
                  <input type="text" className={styles.InputText} 
                      placeholder="Nome del Campione" 
                      value={this.props.username} 
                      onChange={this.props.updateUsernameInfo}/>
                </div>
                

                <div className={styles.InputRow}>
                  <input type="text" className={styles.InputText} 
                      placeholder="Password del Campione" 
                      value={this.props.password} 
                      onChange={this.props.updatePasswordInfo}/>
                </div>
              </div>

              <div className={styles.ButtonDiv}>
                  <button className={styles.LoginButton}
                          onClick={() => this.props.sendLogin(
                                                        this.props.conn.client_id, 
                                                        this.props.conn.socket ,
                                                        this.props.username,
                                                        this.props.password
                                  )}
                  >
                    Login
                  </button>
              </div>
            </ReactModal>
  }
}

export default LoginModal