import React from 'react'

import styles from './PlayersArea.module.css';
import PlayerBoxApp from './box/PlayerBoxApp';


class PlayersArea extends React.Component {

  render() {
    // console.log("PlayersArea - Rendering with props:", this.props)

    let players = this.props.players

    return (
      <div className={styles.PlayersArea}>
        {players && players.map((pl, index) => 
          <PlayerBoxApp key={index} player={pl} />
        )}
      </div>
    )
  }
}

export default PlayersArea