import React from "react";
import { io } from "socket.io-client";
import PlayersAreaApp from "./players/PlayersAreaApp";
import AuctionAreaApp from "./auction/AuctionAreaApp";

import ActionPlayer from "../socket/model/player/ActionPlayer";

import TypeConnectionPlayer from "../socket/model/player/TypeConnectionPlayer";
import TypeTimer from "../socket/model/player/TypeTimer";

import LoginModalApp from "./login/LoginModalApp";

const { v4: uuid_v4 } = require('uuid');

// Connect to the Socket Server in Localhost with namespace: "player"
const port = process.env.PORT || 3000;
console.log("Port is: " + port);

// const SOCKET_HOST = "https://fantatimer-be.adaptable.app/player"
const SOCKET_HOST = "http://localhost:3000/player"
const APPLICATION_HOST = "https://localhost:" + port + "/"
const CLIENT_ID = "client_id"

class App extends React.Component {

  constructor(props) {
    super(props)

    if (typeof (Storage) == "undefined") {
      console.error("Cannot used LocalStorage for Player on this Device")
    }
    // To uniquely identify the Player device
    if (!window.localStorage.getItem(CLIENT_ID)) {
      window.localStorage.setItem(CLIENT_ID, uuid_v4())
    }

    let that = this

    let socket = io(SOCKET_HOST, {
      rejectUnauthorized: false,

      // withCredentials: true,
      extraHeaders: {
        "fantatimer-socket-header": "cors" //define a header to be able to do CORS requests
      },
      autoConnect: true,
      reconnection: true,
      // forceNew: true,
    });

    socket.io.on("error", (error) => {
      console.error("Error with Socket:", error)
    });

    socket.io.on("reconnect_attempt", (count) => {
      console.log(`Reconnection attempt=${count} for Player to Server`)
    });

    socket.io.on("reconnect", (count) => {
      console.log(`Reconnection completed for Player in attempt=${count} for Socket=${socket.id}`)
      socket.emit('player-reconnected', "Player: reconnected!");
    });

    socket.on('connect', function () {
      console.info(`🌍⇄ Connected Player with Socket-Id:`, socket.id);

      // At Handshake, request for Data
      let responseMsg = {
        request_id: uuid_v4(),
        client_id: window.localStorage.getItem(CLIENT_ID)
      }

      // Emit the "Authenticate" Event: ask for loading data
      socket.emit("authenticate", responseMsg);
      console.info(`🌍⇄ Authenticated Player with Message:`, responseMsg);

      that.props.socketConnected(responseMsg.client_id, socket)
    })

    // socket.on('connect', this.onconnect.bind(this, socket));
    socket.on('disconnect', this.ondisconnect.bind(this));
    socket.on('message', this.onmessage.bind(this));
  }

  ondisconnect(reason) {
    // the client was disconnected by the server. The client cannot send messages anymore.
    console.info(`🌍⇹ Disconnected Player with Reason:`, reason);

    this.props.socketDisconnected()

    this.reloadPage()
  }

  reloadPage() {
    window.location.href = APPLICATION_HOST
    window.location.reload();
  }

  async onmessage(msg) {
    try {

      //a message is received, you can access action, destination, sender and payload properties
      console.info(`🌍→ Message received from Server`, msg);

      console.log(`Processing Action=${msg.action} for Message=${msg.request_id}`)

      switch (msg.action) {

        case ActionPlayer.TIMER:
          this.handleTimerAction(msg)
          break

        case ActionPlayer.CONNECTION:
          this.handleConnectionAction(msg)
          break

        /* case ActionPlayer.AUTHENTICATION:
          this.handleAuthenticationAction(msg)
          break */

        default:
          throw new Error(`Nothing to do for ACTION=${msg.action}`)
      }
    } catch (e) {
      // TODO: manage this log
      console.log(e)
      console.error(`Cannot manage Message=${JSON.stringify(msg)}`)
    }
  }

  /* CONNECTION Handling */
  handleConnectionAction(msg) {
    console.log(`HandleConnectionAction: Processing Type=${msg.type} for Action=${msg.action}`)

    let payload = msg.payload  // Payload here is the list of players

    switch (msg.type) {
      case TypeConnectionPlayer.PLAYER_CONNECTED:
        console.log("Processing Type=PLAYER_CONNECTED for Action=CONNECTION")
        this.props.updatePlayersList(payload.players)
        this.props.updateTimerInfo(payload.timer)

        if(payload.new_connected_client_id && payload.new_connected_client_id == this.props.conn.client_id) {
          this.props.updateIsLoggedIn(true)
          this.props.updateUsernameAndPassword(payload.username, payload.password)
        }
        break

      case TypeConnectionPlayer.PLAYER_RECONNECTED:
        console.log("Processing Type=PLAYER_RECONNECTED for Action=CONNECTION")
        this.props.updatePlayersList(payload.players)
        this.props.updateTimerInfo(payload.timer)
        break

      case TypeConnectionPlayer.PLAYER_DISCONNECTED:
        console.log("Processing Type=PLAYER_DISCONNECTED for Action=CONNECTION")
        this.props.updatePlayersList(payload.players)
        this.props.updateTimerInfo(payload.timer)
        break

      default:
        console.error(`Nothing to do for Type=${msg.type} and Action=${msg.action}`)
        return
    }
  }

  /* TIMER Handling */
  handleTimerAction(msg) {
    console.log(`HandleConnectionAction: Processing Type=${msg.type} for Action=${msg.action}`)

    let payload = msg.payload  // Payload here is the list of players

    switch (msg.type) {

      case TypeTimer.SYNCH:
        console.log("Processing Type=TIMER for Action=SYNCH")
        this.props.updateTimerInfo(payload.timer)
        break

      case TypeTimer.EXPIRED:
        console.log("Processing Type=TIMER for Action=EXPIRED")
        this.props.updateTimerInfo(payload.timer)
        break

      default:
        console.error(`Nothing to do for Type=${msg.type} and Action=${msg.action}`)
        return
    }
  }

  render() {

    return (
    <div style={{display: "block", width: "100%", height: "100%"}}>

    {!this.props.isLoggedIn ?
            <LoginModalApp conn={this.props.conn} />
            : ""}
      <PlayersAreaApp />
      <AuctionAreaApp />
    </div>)
  }
}

export default App;