import React from 'react'

import styles from './PlayerBox.module.css';


class PlayerBox extends React.Component {

  render() {
    // console.log("PlayerBox - Rendering with props:", this.props)

    let pl = this.props.player
    let isConnected = pl && pl.socket_id && typeof pl.socket_id !== 'undefined' ? true : false
    let isMyPlayer = pl && pl.client_id && pl.client_id == this.props.clientId

    return (
      <div className={` ${styles.PlayerBox} ${!isConnected ? styles.NotConnectedBox : ""} ${isMyPlayer ? styles.MyPlayerBox : ""} `}>
        <p className={styles.PlayerNameText}>{pl.name}</p>
      </div>
    )
  }
}

export default PlayerBox